<template>
  <div class="bigbox">
    <div class="centerbox">
      <form action="#" class="tzdz">
        <div class="loginbox">
          <h2 style="text-align: center">
            <!-- <img src="../assets/logo.jpg" width="163" height="54" alt /> -->
            智能数字配电网管理平台
          </h2>
          <div class="formbox">
            <!--用户名-->
            <div class="bdbox">
              <span class="icon icon-user"></span>
              <input v-model="lgusername" type="text" class="srk yhm" placeholder="用户名" value />
            </div>
            <!--密码-->
            <div class="bdbox">
              <span class="icon icon-key"></span>
              <input
                v-model="lguserpwd"
                type="password"
                class="srk mm"
                v-on:keydown="enterLoginajax"
                placeholder="密码"
              />
            </div>
            <input
              type="button"
              class="anniu"
              :class="{buttonClass:lgButton}"
              v-on:click="loginajax"
              value="登录"
            />
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import { setToken } from "@/utils/auth";
// import { LoginByUsername } from "@/api"
export default {
  data: function() {
    return {
      lgusername: "",
      lguserpwd: "",
      lgButton: false,
      lgtoken: ""
    };
  },
  watch: {
    lgusername: function() {
      var a = this.lgusername.slice(
        this.lgusername.length - 1,
        this.lgusername.length
      );
      var re = /[A-Za-z]|[\u4e00-\u9fa5]|[0-9]/;
      if (!re.test(a)) {
        this.lgusername = this.lgusername.slice(0, this.lgusername.length - 1);
      }
      if (this.lgusername != "" && this.lguserpwd != "") {
        this.lgButton = true;
      } else {
        this.lgButton = false;
      }
    },
    lguserpwd: function() {
      if (this.lgusername != "" && this.lguserpwd != "") {
        this.lgButton = true;
      } else {
        this.lgButton = false;
      }
    }
  },
  methods: {
    loginajax() {
      //登录验证返回token
      // const params = {
      //   userName: this.lgusername,
      //   password: this.lguserpwd
      // }
      // LoginByUsername(params).then(res=>{
      //   console.log(res);
      // }).catch()
      if (this.lgusername == "admin" && this.lguserpwd == "123456") {
        this.$message({
          message: "登录成功！",
          type: "success"
        });
        setToken("token", "admin");
        this.$router.push("/");
      } else {
        this.$message.error("登录失败，请检查用户名密码是否正确！");
      }
    },
    enterLoginajax(e) {
      //检测密码是否为空之后进行enter事件的监控
      if (e.keyCode == 13) {
        if (this.lgusername != "" && this.lguserpwd != "") {
          this.loginajax();
        }
      }
    }
  }
};
</script>
