<template>
  <div class="homebox">
    <div class="sblb_bar">
      <i class="icon-changzhanzs"></i>
      用户报装
      <div class="fr sbbt_tool">
        <!-- <i class="icon-enlarge" @click="isFull=!isFull"></i> -->
        <i class="icon-loop2" @click="reflash"></i>
        <i class="icon-error sblbguabi" @click="$router.go(-1)"></i>
      </div>
    </div>
    <div>
      <div
        :style="{height:open?'500px':'calc(100vh - 540px)'}"
        style="min-height:200px;overflow:auto;overflow-x: hidden;transition:all linear .2s;"
        class="hiddenx"
      >
        <div id="gxt" style="height:500px;"></div>
      </div>
      <!-- <div style="text-align:center;color:#fff;">
            <i class="el-icon-bottom"></i>
      </div>-->
      <div>
        <div class="view_table">
          <div
            style="text-align:right;border-top:1px solid #234899;height:35px;;line-height:35px;position:relative"
          >
            <i
              class="el-icon-bottom"
              @click="open=true"
              v-if="!open"
              title="展开"
              style="color:#fff;position:absolute;margin:auto;top:0;left:0;bottom:0;right:0;height:30px;width:20px;cursor:pointer"
            ></i>
            <i
              class="el-icon-top"
              @click="open=false"
              v-else
              title="收起"
              style="color:#fff;position:absolute;margin:auto;top:0;left:0;bottom:0;right:0;height:30px;width:20px;cursor:pointer"
            ></i>
            <span style="color:#fff">配电名称：</span>
            <input
              type="text"
              v-model="searchName"
              style="color:#fff;height:30px;background: #576b88;border: 1px solid #aca9c3"
            />
            <el-button size="small" @click="close=true">报装</el-button>
          </div>
          <el-table :data="tableDataxs" height="400" border style="width: 100%">
            <el-table-column prop="id" label="配变编号" width="150" sortable align="center"></el-table-column>
            <el-table-column prop="name" label="配电名称" min-width="160" sortable align="center"></el-table-column>
            <el-table-column prop="name2" label="所属台区" min-width="180" sortable align="center"></el-table-column>
            <el-table-column prop="line" label="所属馈线" sortable min-width="180" align="center"></el-table-column>
            <el-table-column prop="station" label="所属变电站" sortable align="center"></el-table-column>
            <el-table-column prop="type" label="配电类型" sortable align="center"></el-table-column>
            <el-table-column prop="" label="容量" sortable align="center"></el-table-column>
            <el-table-column prop="date" label="日期" sortable align="center"></el-table-column>
            <el-table-column prop="system" label="地区" sortable width="180" align="center"></el-table-column>
          </el-table>
          <div class="t-page" style="text-align:right">
            <el-pagination
              class="t-page"
              current-page="1"
              @current-change="handleCurrentChange"
              :page-sizes="[10,20]"
              :page-size="10"
              layout="total,sizes, prev, pager, next"
              :total="400"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <overlay :close.sync="close">
      <div class="add_trans">
        <div class="sblb_bar">
          <i class="icon-changzhanzs"></i>
          变压器报装
          <div class="fr sbbt_tool">
            <!-- <i class="icon-enlarge" @click="isFull=!isFull"></i> -->
            <i class="icon-loop2"></i>
            <i class="icon-error sblbguabi" @click="close=false"></i>
          </div>
        </div>
        <div class="add_main">
          <el-form ref="form" label-position="left" :model="form" label-width="90px">
            <el-form-item label="所属厂站">
              <el-select v-model="form.subs" placeholder="请选择所属厂站">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="所属线路">
              <el-select v-model="form.lines" placeholder="请选择所属线路">
                <el-option label="区域一" value="shanghai"></el-option>
                <el-option label="区域二" value="beijing"></el-option>
              </el-select>
            </el-form-item>
             <el-form-item label="配电类型">
              <el-select v-model="form.lines" placeholder="请选择配电类型">
                <el-option label="公变" value="shanghai"></el-option>
                <el-option label="专变" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="台区名称">
              <el-input v-model="input" placeholder="请输入内容" style="width:220px;"></el-input>
            </el-form-item>
            <el-form-item label="生效日期">
              <el-date-picker type="date" placeholder="选择日期" v-model="form.date1"  ></el-date-picker>
            </el-form-item>
              <el-button type="primary" @click="onSubmit">立即创建</el-button>
              <el-button>取消</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </overlay>
  </div>
</template>

<script>
import overlay from "@/components/overlay";
import data from "@/mock/tree";
import table1 from '@/mock/table1';
import table2 from '@/mock/table2';
export default {
  data() {
    return {
      tableData: [],
      tableDatamn: table1,
      searchName: "",
      open: false,
      close: false,
      form: {
        subs: "",
        lines:'',
        date1:''
      }
    };
  },
  components: { overlay },
  computed: {
    tableDataxs() {
      let arr = [];
      if (this.searchName === "") return this.tableData;
      else {
        for (let a of this.tableData) {
          if (a.name.indexOf(this.searchName) > -1) arr.push(a);
        }
        return arr;
      }
    }
  },
  mounted() {
    this.tableData = this.tableDatamn;
    this.getEcharts();
  },
  methods: {
    reflash() {
      this.getEcharts();
      this.handleCurrentChange();
    },
    getEcharts() {
      let myChart = this.echarts.init(document.getElementById("gxt"));
      myChart.clear();
      myChart.showLoading();
      myChart.hideLoading();

      this.echarts.util.each(data.children, function (datum, index) {
          index !== 0 && (datum.collapsed = true);
      });
      let option;
      myChart.setOption(
        (option = {
          title: {
            text: "电网局部关系缩略图",
            textStyle: {
              color: "#fff",
              fontSize: 12
            }
          },
          tooltip: {
            trigger: "item",
            triggerOn: "mousemove"
          },
         
          series: [
            {
              type: "tree",
              data: [data],
              top: "1%",
              left: "7%",
              bottom: "1%",
              right: "20%",

              symbolSize: 10,
              label: {
                normal: {
                  position: "left",
                  verticalAlign: "middle",
                  align: "right",
                  fontSize: 12,
                  color: "#4ec4ff"
                }
              },

              leaves: {
                label: {
                  normal: {
                    position: "right",
                    verticalAlign: "middle",
                    align: "left",
                    fontSize: 12,
                    color: "#4ec4ff"
                  }
                }
              },
              expandAndCollapse: true,
              initialTreeDepth:-1,
              // animationDuration: 550,
              // animationDurationUpdate: 750
            }
          ]
        })
      );
      const that = this;
      myChart.on('click',function(params){
        console.log(params)
        if (params.data.name =='10kV栗南线栗15') {
          that.tableData = table2;
        }else if(params.data.name ==="10kV栗马线栗16"){
          that.tableData = table1;
        }
      })
    },
    handleCurrentChange() {
      this.tableData = [];
      setTimeout(() => {
        this.tableData = this.tableDatamn;
      }, 100);
    },
    onSubmit() {
        this.$message({
          message: '成功提交台区报装信息！',
          type: 'success'
        });
        this.close= false;
      }
  }
};
</script>

<style>
.el-table__header-wrapper table {
  margin: 0;
}
.hiddenx .el-scrollbar__wrap {
  overflow-x: hidden;
}
/* 滚动条 */
::-webkit-scrollbar {
  width: 10px;
  height: 12px;
}

::-webkit-scrollbar-thumb {
  background-color: #2e3953;
}
.el-pager li,
.btn-prev,
.btn-next {
  background: none !important;
}
.el-select .el-input .el-input__inner {
  background: none;
}
.add_trans {
  width: 750px;
}
.add_main{
    width: 75%;
    margin: 0 auto;
    padding: 18px 0;
}
</style>

